* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;

  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
html {
  scroll-behavior: smooth;
  font-size: 10px;
}
header {
  width: min(100%, 1200px);
  display: flex;
  justify-content: space-between;
}
h1 {
  font-weight: 300;
  font-size: 3.2rem;
  line-height: 5rem;
  color: white;
}
h2 {
  font-size: 3.6rem;
  font-weight: 500;
  padding-bottom: 20px;
  line-height: 4.6rem;
}
p {
  font-size: 2rem;
  line-height: 2.6rem;
  font-weight: 100;
  text-align: justify;
}
#logo {
  height: 50px;
  vertical-align: middle;
  margin-right: 30px;
}
#about ul {
  display: flex;
}
#about li {
  margin-left: 70px;
  line-height: 3rem;
  padding: 10px 15px;
}
a {
  text-decoration: none;
  color: inherit;
  font-size: 1.8rem;
  font-weight: 100;
}
li {
  list-style: none;
  color: white;
}
li#sb-contact {
  padding: 2px 0;
}
#sb-contact button {
  background: transparent;
  color: white;
  padding: 8px 30px;
  border: 1px solid white;
  outline: none;
  line-height: 3.2rem;
  font-size: 1.8rem;
  font-weight: 100;
}
div.parts {
  width: 100%;
  min-height: 100vh;
  height: auto;
  background: linear-gradient(#141417, #151519);
}

div.partsContent {
  padding: 40px 50px 60px 50px;
  min-height: 100vh;
  color: white;
  width: 100%;
  height: 100%;
  max-width: 1450px;
  position: relative;
  margin: auto;
}
#about {
  display: flex;
}
#about main {
  width: 30%;
  padding: 20px 0;
  /* position: absolute; */
  margin-top: 25%;
}
.parts-background-image {
  width: 100%;
  background-image: url("./images/about-blue-logo.png");
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: auto 85%;
}
.servWrapper {
  flex-wrap: wrap;
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  min-height: Calc(100vh - 250px);
  padding-bottom: 30px;
}
#services p {
  font-size: 3rem;
  line-height: 3.8rem;
  margin: 20px 0;
}
.service {
  border: 1px solid white;
  border-radius: 50px;
  padding: 0 20px;
  width: 30%;
  min-width: 340px;
  overflow: hidden;
  vertical-align: middle;
  display: flex;
  align-items: center;
}
.service h2 {
  font-size: 5rem;
  line-height: 5.6rem;
  margin: 20px 0;
}
.part-title {
  width: 100%;
  font-size: 5rem;
  padding: 25px 0;
  margin-bottom: 40px;
}
.servContent {
  width: 100%;
  margin: auto;
}
#contact main {
  display: flex;
  justify-content: space-between;
}
#contact form {
  width: 400px;
  display: flex;
  flex-direction: column;
}
#contact input,
#contact button,
#contact textarea {
  width: 100%;
  padding: 16px 15px;
  font-size: 2.4rem;
  font-weight: 100;
  border: none;
  border-radius: 12px;
  display: block;
  margin-bottom: 30px;
  outline: none;
}
#contact button {
  background: #004dff;
  color: white;
  font-size: 2rem;
  cursor: pointer;
}
#contact span {
  font-size: 1.8rem;
  font-weight: 100;
  position: absolute;
  margin-top: -60px;
}
#contact ul {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 15%;
  /* display: none; */
}
#contact li a {
  padding-right: 50px;
  display: flex;
  font-size: 2.2rem;
  line-height: 4rem;
  font-weight: 100;
  margin-top: 10px;
}
#contact li img {
  height: 25px;
  margin: 5px;
}
#contact .partsContent {
  background-image: url("./images/contact-blue-logo.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto 50%;
}

.arrowDown {
  /* background: red; */
  position: absolute;

  text-align: center;
  bottom: 10px;
  width: 100%;
  left: 0;
}
.arrowUp {
  /* background: red; */
  position: absolute;
  /* padding-right: 10%; */
  text-align: right;
  top: 20px;
  width: 100%;
  left: 0;
}
.arrowDown img,
.arrowUp img {
  width: 3%;
  min-width: 20px;
  margin: 0 10px;
}

@media (max-width: 1200px) {
  header {
    width: 100%;
    flex-direction: column;
  }
  h1 {
    margin: auto;
  }
  div.parts {
    min-height: 100vh;
    height: auto;
  }
  header ul {
    width: auto;
    flex-direction: column;
    margin-top: 40px;
  }
  #about li {
    width: 100%;
    margin: auto;
    border-bottom: 1px solid white;
    text-align: center;
  }
  li#sb-contact {
    border-bottom: none;
  }
  #sb-contact button {
    border: none;
    padding: 10px 15px;
  }
  #about main {
    width: 100%;
  }
  #about .parts-background-image {
    background-size: 70%;
  }
  .servWrapper {
    height: 100%;
    flex-direction: column;
  }
  div.partsContent {
    padding: 5%;
    height: auto;
    min-height: 100vh;
    width: 100vw;
  }

  div.service {
    width: 100%;
    min-width: 100%;
    height: 30%;
    margin: 10px auto;
    min-height: 500px;
  }
  #contact main {
    flex-direction: column;
    position: relative;
    height: Calc(100% - 100px);
  }
  #contact form {
    width: 100%;
  }
  #contact ul {
    padding: 0;
  }
}

@media (max-width: 400px) {
  html {
    font-size: 6px;
  }
}
